.form-sedona {
  width: 320px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  position: relative;
  padding-top: 24px;
}
@media (min-width: 768px) {
  .form-sedona {
    width: 648px;
    margin: 0 auto;
    display: grid;
    position: relative;
    padding-top: 0;
    padding-bottom: 64px;
    grid-template-areas: "person info" "impression  impression" "visit visit" "emotions emotions";
    row-gap: 45px;
  }
  .form-sedona .span-hidden {
    font-size: 14px;
  }
  .form-sedona .form-person {
    grid-area: person;
    width: 280px;
    padding-bottom: 0;
  }
  .form-sedona .form-person .form-person__title {
    width: 160px;
    margin-bottom: 28px;
    font-size: 21px;
  }
  .form-sedona .form-person::after {
    content: "";
    border-right: 2px solid #eeeeee;
    position: absolute;
    height: 338px;
    left: 315px;
  }
  .form-sedona .form-info {
    grid-area: info;
    width: 298px;
    display: flex;
    margin-left: auto;
    padding-bottom: 0;
  }
  .form-sedona .form-info .form-info__title {
    width: 278px;
    margin-bottom: 28px;
    font-size: 21px;
  }
  .form-sedona .form-emotions {
    grid-area: emotions;
    width: 648px;
    padding-bottom: 0;
  }
  .form-sedona .form-emotions .form-emotions__block {
    width: 100%;
    height: 160px;
    margin-bottom: 46px;
  }
  .form-sedona .form-emotions .form-emotions__title {
    width: 402px;
    margin-bottom: 28px;
    font-size: 21px;
  }
  .form-sedona .button-sedona {
    width: 324px;
    margin: 0;
  }
  .form-sedona .form-visited {
    grid-area: visit;
    width: 648px;
    padding-bottom: 0;
  }
  .form-sedona .form-visited .form-visited__block {
    display: grid;
    grid-template-columns: repeat(2, 224px);
    justify-content: center;
  }
  .form-sedona .form-visited .form-visited__title {
    margin-bottom: 28px;
    width: 308px;
    font-size: 21px;
  }
  .form-sedona .form-impression {
    grid-area: impression;
    width: 648px;
    padding-bottom: 0;
  }
  .form-sedona .form-impression .form-impression__block {
    display: grid;
    grid-template-columns: repeat(2, 280px);
    justify-content: center;
  }
  .form-sedona .form-impression .form-impression__title {
    width: 402px;
    margin-bottom: 28px;
    font-size: 21px;
  }
}
@media (min-width: 1280px) {
  .form-sedona {
    width: 960px;
    margin: 0 auto;
    display: grid;
    position: relative;
    padding-top: 0;
    padding-bottom: 64px;
    grid-template-areas: "person info" "impression  impression" "visit visit" "emotions emotions";
    row-gap: 56px;
  }
  .form-sedona .form-person {
    grid-area: person;
    width: 430px;
    padding-bottom: 15px;
  }
  .form-sedona .form-person::after {
    left: 493px;
  }
  .form-sedona .form-info {
    grid-area: info;
    width: 402px;
  }
  .form-sedona .form-impression {
    grid-area: impression;
    width: 100%;
  }
  .form-sedona .form-impression .form-impression__block {
    display: grid;
    grid-template-columns: 280px 280px 250px;
    /* justify-content: space-around; */
    justify-content: space-between;
    margin: 0;
    padding-left: 53px;
    padding-right: 53px;
  }
  .form-sedona .form-impression .form-impression__title {
    margin-bottom: 45px;
  }
  .form-sedona .form-visited {
    grid-area: visit;
    width: 100%;
  }
  .form-sedona .form-visited .form-visited__block {
    display: grid;
    grid-template-columns: 180px 178px 161px 184px;
    justify-content: space-between;
    margin: 0;
    padding-left: 45px;
    padding-right: 45px;
  }
  .form-sedona .form-visited .form-visited__title {
    margin-bottom: 59px;
  }
  .form-sedona .form-emotions {
    grid-area: emotions;
    width: 768px;
    margin: 0 auto;
  }
  .form-sedona .button-sedona {
    width: 384px;
  }
}
